import React from 'react';

const style = {
  footerFormat: {
    justifyContent: "center",
    textAlign: "center",
    lineHeight: "30px",
    marginTop: "80px",
    marginBottom: "30px",
  },
  footer: {
    justifyContent: "center",
    textAlign: "center",
    fontSize: "2em",
  },
}

function Footer() {
  return(
  <div style={style.footerFormat} id="contact">
    <h3 className='footer'>Additional Information</h3>
    <ul className="footer">
      <li className="email">Email: kolemazur@gmail.com</li>
      <li className="footer-links">
        <a href="https://github.com/NikolasMazur" target="_blank">GitHub</a>
      </li>
    </ul>
  </div>
  );
};

export default Footer;